@supports (display:grid)
{
    @media screen and (min-width:800px) {
        #main{
            grid-template-rows:830px 500px 300px 2000px!important;
        }
    }
    @media screen and (min-width:900px) {
        #main{
            grid-template-rows:770px 500px 300px 2000px!important;
        }
    }
    @media screen and (min-width:1000px) {
        #main{
            grid-template-rows:710px 500px 300px 2000px!important;
        }
    }
    @media screen and (min-width:1200px) {
        #main{
            grid-template-rows:650px 500px 300px 2000px!important;
        }
    }
    @media screen and (min-width:700px) {

        #main{
            display: grid;
            grid-template-columns: 40% 5% 55%;
            grid-template-rows:900px 500px 300px 2000px;
            margin:10px;
        }
        .about{
            grid-row: 1;
            grid-column: 1;
        }
        .skills{
            grid-row: 2;
            grid-column: 1;
        }
        .form{
            grid-row: 3;
            grid-column: 1;
        }
        .lang{
            grid-row: 4;
            grid-column: 1;
        }
        .exp{
            grid-row-start: 1;
            grid-row-end:4;
            grid-column: 3;
            overflow: visible;
        }
    }}


